footer {
  display: block;
  text-align: center;
  bottom: 0;
  background: -webkit-linear-gradient(left, #7000ff, #ff00f5);
  width: 100vw;
  max-width: 100%;
  padding: 1.5% 15%;
  color: white;
}

header {
  display: block;
  width: 100vw;
  max-width: 100%;
}

.header-logo {
  font-size: 1.7em !important;
  background: -webkit-linear-gradient(left, #7000ff, #ff00f5);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
